import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { ClaimGuard } from 'src/app/guards/claim-guard/claim.guard';
import { FormsModule } from '@angular/forms';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatIconModule } from '@angular/material/icon';
import { TranslateModule } from '@ngx-translate/core';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { ClaimSettingsFraudDetectionComponent } from './claim-settings-fraud-detection/claim-settings-fraud-detection.component';
import { ClaimSettingsGaragesComponent } from './claim-settings-garages/claim-settings-garages.component';
import { ClaimSettingsLayoutComponent } from './claim-settings-layout/claim-settings-layout.component';

const claimRoutes: Route[] = [
  {
    path: '',
    canActivate: [ ClaimGuard ],
    component: ClaimSettingsGaragesComponent,
  },
  {
    path: 'fraud-detection',
    canActivate: [ ClaimGuard ],
    component: ClaimSettingsFraudDetectionComponent,
  }
];

@NgModule({
  declarations: [
    ClaimSettingsLayoutComponent,
    ClaimSettingsGaragesComponent,
    ClaimSettingsFraudDetectionComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(claimRoutes),
    WidgetsModule,
    SharedModule,
    MatDatepickerModule,
    MatIconModule,
    TranslateModule,
    FormsModule
  ],
  exports: [ RouterModule, ClaimSettingsLayoutComponent,
    MatDatepickerModule ]
})
export class ClaimSettingsLayoutModule { }
